.activity-tile{
    width: 100%;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 400;
    padding: 6px 17px;
    height: 40px;
    box-shadow: rgba(0, 0, 0, 0.24) 7px 5px 5px;
    cursor: default;
    color: var(--primary-text);
}

.activity-tile>b{
    font-weight: 500;
}


.faded{
    opacity: 0.5;
}